import React, { useEffect, useState } from "react";
import db from "../utils/db";
import Header from "../components/Header";
import { faPencilAlt } from '@fortawesome/free-solid-svg-icons';

function WordScreen(props) {
	const [word, setWord] = useState({});

	const { id } = props.match.params;

	useEffect(() => {
		db.words.get(id).then((word) => {
			setWord(word);
		});
	}, []); // eslint-disable-line

	return (
		<div id="word-screen">
			<Header
				title="Review"
				leftIcon="back"
				onLeftIconClick={() => { props.history.push('/word-list'); }}
				rightIcon={faPencilAlt}
				onRightIconClick={() => {
					props.history.push(`/edit-word/${word.id}`);
				}}
			/>
			<div className="body">
				<div className="content">
					<h2>{word.title}</h2>
					<p>{word.translation}</p>

					<p className="label">Part of speech</p>
					<p>{word.partOfSpeech}</p>

					<p className="label">Sentence where found</p>
					<p>{word.sentence}</p>

					<p className="label">My own sentence</p>
					<p>{word.mySentence}</p>
				</div>
				<a
					className="button"
					href={`http://www.google.com/search?q=${word.title} meaning`}
					target="_blank"
					rel="noopener noreferrer"
				>
					<img
						src={require('../assets/images/google-logo.png')}
						alt="google search logo"
					/>
					<div>
						Search on Google
					</div>
				</a>
				<a
					className="button"
					href={`https://translate.google.com/#auto/fa/${word.title}`}
					target="_blank"
					rel="noopener noreferrer"
				>
					<img
						src={require('../assets/images/translate-logo.png')}
						alt="google translate logo"
					/>
					<div>
						Translate To Farsi
					</div>
				</a>
			</div>
		</div>
	);
}

export default WordScreen;