import React from "react";
import Header from "../components/Header";

function ContactScreen(props) {
	return (
		<div id="contact-screen">
			<Header
				title="About"
				leftIcon="back"
				onLeftIconClick={() => { props.history.push('/'); }}
			/>
			<div className="body">
				<p>You can send your reviews and feedbacks to the email below.</p>
				<p>
					email: <a href="mailto:pheromona13@gmail.com">pheromona13@gmail.com</a>
				</p>
			</div>
		</div>
	);
}

export default ContactScreen;