import React, { useEffect, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faQuestion, faEnvelope } from "@fortawesome/free-solid-svg-icons";
import tools from "../utils/tools";

function Drawer() {
	const [greetings, setGreetings] = useState(calculateGreetings());

	useEffect(() => {
		setDrawerWrapperEvents();

		// update greetings message every one minute
		setInterval(() => {
			setGreetings(calculateGreetings());
		}, 60 * 1000);
	});

	function setDrawerWrapperEvents() {
		const wrapper = document.querySelector(".drawer-wrapper");

		wrapper.addEventListener("transitionend", OnTransitionEnd, false);
		wrapper.addEventListener("click", tools.drawer.toggle, false);

		function OnTransitionEnd() {
			wrapper.classList.remove("drawer-wrapper--animatable");
		}
	}
	function calculateGreetings() {
		const date = new Date();

		if (date.getHours() >= 5 && date.getHours() < 12)
			return 'Good Morning!';
		else if (date.getHours() >= 12 && date.getHours() < 17)
			return 'Good Afternoon!';
		else
			return 'Good Evening!';
	}

	return (
		<div className="drawer-wrapper">
			<div className="drawer-content">
				<div className="drawer-header">
					{greetings}
				</div>
				<div className="drawer-body">
					<div className="title">Menu</div>
					<Link to="/about" className="drawer-menu-item">
						<FontAwesomeIcon icon={faQuestion} />
						<span>About</span>
					</Link>
					<Link to="/contact" className="drawer-menu-item">
						<FontAwesomeIcon icon={faEnvelope} />
						<span>Contact</span>
					</Link>
					<footer>
						Version {process.env.REACT_APP_VERSION}
					</footer>
				</div>
			</div>
		</div>
	);
}

export default withRouter(Drawer);