import React, { useEffect, useState } from "react";
import db from "../utils/db";
import { Link } from "react-router-dom";
import Header from "../components/Header";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faPencilAlt, faEllipsisV, faCheck, faSearch } from "@fortawesome/free-solid-svg-icons";

function WordListScreen(props) {
	const [words, setWords] = useState([]);
	const [displayWords, setDisplayWords] = useState([]);
	const [searchInput, setSearchInput] = useState('');

	useEffect(() => {
		db.table('words')
			.toArray()
			.then((words) => {
				setWords(words);
				setDisplayWords(words);
			});
	}, []);

	return (
		<div id="word-list-screen">
			<Header
				title="Review"
				mainHeaderStyle={{ alignItems: 'baseline' }}
				leftIcon="back"
				onLeftIconClick={() => { props.history.push('/'); }}
				SubHeader={
					<div className="search-container">
						<input
							type="text"
							placeholder="Search word"
							value={searchInput}
							onChange={e => {
								setSearchInput(e.target.value);
								setDisplayWords(
									words.filter((word) => {
										return word
											.title
											.toLowerCase()
											.includes(e.target.value.toLowerCase());
									})
								);
							}}
						/>
						{!searchInput && <FontAwesomeIcon icon={faSearch} />}
						{searchInput && (
							<FontAwesomeIcon
								icon={faTimes}
								className="clear"
								onClick={() => {
									setSearchInput('');
									setDisplayWords(words);
								}}
							/>
						)}
					</div>
				}
			/>
			<div className="body">
				<div className="filter">Displaying {displayWords.length}/{words.length} words</div>

				{displayWords.map((word, index) => {
					return (
						<div className="word-card" key={index}>
							{word.actions === 'confirmRemove' ? (
								<div className="content">
									Are you sure you want to delete this?
								</div>
							) : (
									<Link to={`/word/${word.id}`} className="content">
										<div>
											{word.title}
										</div>
									</Link>
								)
							}
							<div className="actions">
								{word.actions === 'confirmRemove' && (
									<React.Fragment>
										<div
											className="action-button"
											onClick={() => {
												db.table('words')
													.delete(word.id)
													.then(() => {
														const newWords = words.filter((item) => item.id !== word.id);
														setWords(newWords);
														setDisplayWords(newWords);
													});
											}}
										>
											<FontAwesomeIcon icon={faCheck} className="yes" />
										</div>
										<div
											className="action-button"
											onClick={() => {
												let newWords = [...words];
												newWords[index].actions = 'modify';
												setWords(newWords);
											}}
										>
											<FontAwesomeIcon icon={faTimes} className="no" />
										</div>
									</React.Fragment>
								)}
								{word.actions === 'modify' && (
									<React.Fragment>
										<Link to={`/edit-word/${word.id}`} className="action-button">
											<div>
												<FontAwesomeIcon icon={faPencilAlt} className="edit" />
											</div>
										</Link>
										<div
											className="action-button"
											onClick={() => {
												let newWords = [...words];
												newWords[index].actions = 'confirmRemove';
												setWords(newWords);
												setDisplayWords(newWords);
											}}
										>
											<FontAwesomeIcon icon={faTimes} className="remove" />
										</div>
									</React.Fragment>
								)}
								{!word.actions && (
									<div
										className="action-button"
										onClick={() => {
											let newWords = [...words];
											newWords[index].actions = 'modify';
											setWords(newWords);
										}}
									>
										<FontAwesomeIcon icon={faEllipsisV} className="more" />
									</div>
								)}
							</div>
						</div>
					);
				})}
			</div>
		</div>
	);
}

export default WordListScreen;