import React from "react";
import Header from "../components/Header";

function AboutScreen(props) {
	return (
		<div id="about-screen">
			<Header
				title="About"
				leftIcon="back"
				onLeftIconClick={() => { props.history.push('/'); }}
			/>
			<div className="body">
				<p>ACTIVE Skills for Reading uses thematically organized non-fiction reading passages to teach reading comprehension and vocabulary skills. Written by reading specialist Neil J. Anderson, this innovative series uses an ACTIVE reading methodology to help learners become more confident,  independent – and  active – readers.</p>
				<p>This is an app to record and review your list of words based on ACTIVE skills for reading method.</p>
			</div>
		</div>
	);
}

export default AboutScreen;