import React from 'react';
import './App.scss';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import HomeScreen from './screens/home/HomeScreen';
import AboutScreen from './screens/AboutScreen';
import ContactScreen from './screens/ContactScreen';
import AddWordScreen from './screens/AddWordScreen';
import WordListScreen from './screens/WordListScreen';
import WordScreen from './screens/WordScreen';
import Drawer from './components/Drawer';
import EditWordScreen from './screens/EditWordScreen';

function App() {
	return (
		<div className="container">
			<Router basename={process.env.PUBLIC_URL}>
				<Drawer />
				<Route exact path="/" component={HomeScreen} />
				<Route path="/about" component={AboutScreen} />
				<Route path="/contact" component={ContactScreen} />
				<Route path="/add-word" component={AddWordScreen} />
				<Route path="/word-list" component={WordListScreen} />
				<Route path="/word/:id" component={WordScreen} />
				<Route path="/edit-word/:id" component={EditWordScreen} />
			</Router>
		</div>
	);
}

export default App;
