import React, { useState, useEffect } from "react";
import db from "../utils/db";
import Header from "../components/Header";
import Alert from "../components/Alert";

function EditWordScreen(props) {
	const [title, setTitle] = useState('');
	const [translation, setTranslation] = useState('');
	const [partOfSpeech, setPartOfSpeech] = useState('');
	const [sentence, setSentence] = useState('');
	const [mySentence, setMySentence] = useState('');
	const [alertVisibility, setAlertVisibility] = useState(false);
	const [fetchLoading, setFetchLoading] = useState(false);

	const { id } = props.match.params;
	const partsOfSpeech = [
		'Noun',
		'Pronoun',
		'Verb',
		'Adjective',
		'Adverb',
		'Preposition',
		'Conjunction',
		'Interjection'
	];

	useEffect(() => {
		db.words.get(id).then((word) => {
			setTitle(word.title);
			setTranslation(word.translation);
			setPartOfSpeech(word.partOfSpeech);
			setSentence(word.sentence);
			setMySentence(word.mySentence);
		});
	}, []); // eslint-disable-line

	function editWord() {
		db.table('words')
			.update(id, {
				id: id,
				title: title,
				translation: translation,
				partOfSpeech: partOfSpeech,
				sentence: sentence,
				mySentence: mySentence
			})
			.then(() => {
				setAlertVisibility(true);

				setTimeout(() => {
					setAlertVisibility(false);
				}, 2000);
			});
	}

	return (
		<div id="add-word-screen">
			<Header
				title="Edit Word"
				leftIcon="back"
				onLeftIconClick={() => { props.history.push('/word-list'); }}
			/>
			<Alert show={alertVisibility}>
				Changes saved succesfully!
			</Alert>
			<div className="body">
				<div className="form-group">
					<label>Word</label>
					<input
						type="text"
						className="form-control"
						value={title}
						onChange={e => { setTitle(e.target.value); }}
					/>
				</div>
				<div className="form-group">
					<button
						type="button"
						disabled={!title || fetchLoading}
						onClick={() => {
							const token = '17bb4139-9ab8-44ee-b920-8b7f002f11cd';
							const url = `https://dictionaryapi.com/api/v3/references/collegiate/json/${title}?key=${token}`;

							setFetchLoading(true);
							return fetch(url)
								.then((response) => response.json())
								.then((result) => {
									if (result && result.length) {
										setTranslation(
											result
												.map((item) => { return item.shortdef.join('\n'); })
												.reverse()
												.join('\n-----------\n')
										);
									}
								})
								.finally(() => {
									setFetchLoading(false);
								});
						}}
					>
						Get definition online
					</button>
					<label>Translation</label>
					<textarea
						className="form-control"
						rows="6"
						value={translation}
						onChange={e => { setTranslation(e.target.value); }}
					/>
				</div>
				<div className="form-group separator">
					<label>Extra informations</label>
				</div>
				<div className="form-group">
					<label>Part of Speech</label>
					<select
						className="form-control"
						value={partOfSpeech}
						onChange={e => { setPartOfSpeech(e.target.value); }}
					>
						<option value=""></option>
						{partsOfSpeech.map((item, index) => {
							return <option key={index} value={item}>{item}</option>;
						})}
					</select>
				</div>
				<div className="form-group">
					<label>Sentence where found</label>
					<textarea
						className="form-control"
						rows="3"
						value={sentence}
						onChange={e => { setSentence(e.target.value); }}
					/>
				</div>
				<div className="form-group">
					<label>My own sentence</label>
					<textarea
						className="form-control"
						rows="3"
						value={mySentence}
						onChange={e => { setMySentence(e.target.value); }}
					/>
				</div>
			</div>
			<div className="footer">
				<button type="button" onClick={editWord}>
					Save
				</button>
			</div>
		</div>
	);
}

export default EditWordScreen;