import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faBars } from '@fortawesome/free-solid-svg-icons';
import tools from "../utils/tools";

function Header(props) {
	function getLeftIcon() {
		switch (props.leftIcon) {
			case 'menu':
				return faBars;

			case 'back':
				return faArrowLeft;
		}
	}
	function onLeftIconClick() {
		switch (props.leftIcon) {
			case 'menu':
				tools.drawer.toggle();
				break;

			default:
				props.onLeftIconClick();
				break;
		}
	}

	return (
		<div className="header">
			<div className="main-header" style={props.mainHeaderStyle}>
				<div
					className="icon-container"
					onClick={props.leftIcon && onLeftIconClick}
				>
					{props.leftIcon && (
						<FontAwesomeIcon icon={getLeftIcon()} />
					)}
				</div>
				<h1>
					{props.title}
				</h1>
				<div
					className="icon-container"
					onClick={props.rightIcon && props.onRightIconClick}
				>
					{props.rightIcon && (
						<FontAwesomeIcon icon={props.rightIcon} />
					)}
				</div>
			</div>
			<div className="sub-header">
				{props.SubHeader}
			</div>
		</div>
	);
}

export default Header;