function toggleDrawer() {
	const wrapper = document.querySelector(".drawer-wrapper");

	wrapper.classList.add("drawer-wrapper--animatable");
	if (!wrapper.classList.contains("drawer-wrapper--visible")) {
		wrapper.classList.add("drawer-wrapper--visible");
	} else {
		wrapper.classList.remove('drawer-wrapper--visible');
	}
}

export default {
	drawer: {
		toggle: toggleDrawer
	}
}